.skill{
}
.skill-container{
    max-width: 1284px;
    margin: 0px auto;
}
.skill-header{
    text-decoration: underline;
    font-weight: bold;
    letter-spacing: 2px;
    text-align: center;
    margin-top: 32px;
}
.skill-grid-container{
    width: 100%;
    margin-top: 40px;
    display: grid;
    justify-content: center;
    /* height: 100vh; */
    place-items: center;
    grid-template-columns: repeat(auto-fill, minmax(200px, 1fr));
    /* grid-template-columns: 1fr 1fr 1fr 1fr; */
    gap: 40px;
}
.skill-grid-container img{
    width: 200px;
    height: 200px;
}
@media screen and (min-width:1600px) {
    .skill-container {
        max-width: 1584px;
    }
}
@media screen and (max-width:1400px) {
    .skill-container{
        max-width: 1192px;
    } 
}
@media screen and (max-width:1400px) {
    .skill-container{
        max-width: 1292px;
    } 
}
@media screen and (max-width:1200px) {
    .skill-container{
        max-width: 962px;
    } 
}
@media screen and (max-width:768px) {
    .skill-container{
        max-width: 562px;
        margin: 10px auto;
    } 
    /* .skill-grid-container{
        grid-template-columns: 1fr 1fr 1fr;
    } */
}
@media screen and (max-width:525px) {
    .skill-container{
        max-width: 362px;
        margin: 10px auto;
    } 
    /* .skill-grid-container{
        grid-template-columns: 1fr 1fr;
    } */
}