.navbar{
 background-color: black;
 display: flex;
 flex-direction: column;
}
.nav-brand-container{
    background-color: white;
    display: flex;
    align-items: center;
    height: 50px;
}
.nav-brand,.nav-contact{
    flex: 1;
}
.nav-brand{
   position: relative;
   left: 20px;
}
.nav-brand img{
    cursor: pointer;
}
.nav-contact{
    display: flex;
   justify-content: end;
}
.navbar-container{
    flex: 1;
    display: flex;
    justify-content: flex-end;
    margin: 0px;
}
.nav-contact-details,.nav-gmail-details{
    display: flex;
    cursor: pointer;
    align-items: center;
}
.nav-contact-details p,.nav-gmail-details p{
    margin-left: 10px;
}
.nav-gmail-details{
    margin-left: 15px;
}
.navbar-container li{
    list-style: none;
    padding: 16px 24px;
    color: rgb(239, 239, 65);
    cursor: pointer;
}
@media screen and (max-width:768px) {
    .navbar-container li{
        padding: 16px;
    }
}
@media screen and (max-width:386px) {
    .navbar-container li{
        font-size: 11px;
    }
    /* .nav-brand-container,.nav-contact{
        flex-direction: column;
        height: auto;
        align-items: flex-start;
    } */
    .nav-contact-details p,.nav-gmail-details p{
        font-size: 14px;
    }
    .nav-brand{
        position: relative;
        left: 5px;
     }
     .nav-contact-details p,.nav-gmail-details p{
        margin-left: 2.5px;
    }
    .nav-gmail-details{
        margin-left: 7.5px;
    }
}