.image-hover-container{
    width: 100%;
    height: 100%;
    position: relative;
}
.overlay-img {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background-color: rgba(0, 0, 0, 0); /* Adjust alpha for darkness */
    pointer-events: none;
    transition: background-color 0.2s ease , opacity 0.2s ease;
  }
.text {
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translateX(-50%);
    color: #fff;
    font-size: 34px;
    opacity: 0;
    transition: opacity 0.4s ease, transform 0.8s ease;
  }
  
.text.visible {
    opacity: 1;
    transform: translate(-50%, -50%);
}

.image-hover-container img:hover + .overlay-img{
    background-color: rgba(0, 0, 0, 0.4);
}
.img-one .image-hover-container:nth-child(2){
    margin-top: 16px;
}
@media screen and (max-width:768px) {
    .text {
        font-size: 16px;
    }
}