

.gif-container{
   display: flex;
   justify-content: center;
   align-items: center;
}
.gif-container img{
    width: 400px;
    height: 400px;
}
.gif-section{
    background-color: yellow; 
    padding: 40px 0px;
    text-align: center;
}
.gif-head h3{
    max-width: 400px;
    margin: 0px;
    color:black;
}
.gif-section p{
    font-size: 18px;
    font-weight: 600;
}
.gif-head{
    display: flex;  
    justify-content: center;
    align-items: center;
}
/* .gif-section h3{
    width: 400px;
    text-align: center;
} */
@media screen and (max-width:525px) {
    .gif-container img{
        width: 250px;
        height: 250px;
    }
}