.service-content{
    display: flex;
    flex-direction: column;
}
.service-head{
    text-align: center;
}
.service-head{
    -webkit-text-stroke-color: black;
    -webkit-text-stroke-width: 1px;
}
.service-head h3{
    font-size: 32px;
    font-weight: bold;
}
.service-head p{
    font-size: 24px;
    font-weight: 500;
}
.image-service-wrap,.image-service-container{
    display: flex;
}
.img-one{
    font-size: 0px;
    position: relative;
    width: 60%;
    height: 100%;
    margin-right: 20px;
}
.img-one img{
    object-fit: cover;
    height: 100%;
}
.img-two{
    font-size: 0px;
    position: relative;
    width: 40%;
}
.img-three{
    margin-right: 20px;
}
.img-three,.img-four{
    position: relative;
    margin-top: 20px;
    width: 49.5%;
}

@media screen and (max-width:768px) {
    .image-service-wrap{
        flex-direction: column;
    }
    .img-one,.img-two{
        width: 100%;
        height: 100%;
        margin:0px;
    }
    .img-two{
        margin-top: 16px;
    }
    .img-one img:nth-child(2){
        margin-top: 16px;
    }
    .img-three,.img-four{
        margin-top: 16px;
    }
    .image-service-wrap,.image-service-container{
        margin: 0px 20px;
    }
    
}

.content {
    opacity: 0;
    transform: translateX(-100%);
    transition: opacity 0.5s ease, transform 0.5s ease;
}
  
/* Intersection effect */
.content.show-content {
    opacity: 1;
    transform: translateX(0);
}

  